header{
    min-height: 70px;
    width: 100%;
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    border-bottom: 1px solid #ffffff;
}
header .logo{
    flex: 1;
}

.logo .title-logo{
    margin-left: 5px;
    margin-bottom: 50;
    font-size: 1.7rem;
    text-align: start;
}

header a{
    margin-left: 5px;
    margin-bottom: 50;
    text-transform: uppercase;
    color: #555;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}


header ul li{
    display: inline-block;
    opacity: 0.7;
    padding: 0 20px;

}
header ul li:hover{
    opacity: 1;
    border-bottom: 2px solid #057933;
}


.menu{
    display: none;
}
.cart-icon{
    position: relative;
    margin-right: 20px;
}


.cart-icon span{
    background: crimson;
    border-radius: 20px;
    color: white;
    position: absolute;
    top:-10px;
    right: -10px;
    padding: 5px 7px;
    font-size: 10px;
}

.login-letter{
    font-size: 20px;
}